.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  background: url(../../public/assets/bg2.jpg);

  .title-container {
    margin-bottom: 4vh;
    height: 72px;

    .title {
      font-size: 3rem;
      font-weight: 600;
      color: rgb(255, 255, 255);
      text-shadow: rgb(0 0 0 / 50%) 2px 2px 8px;
    }
  }


  .btn-wrapper {
    display: flex;
    align-items: center;

    >* {
      color: #403f3f;
      font-weight: 600;
      background-color: #e5e7eb;

      &+* {
        margin-left: 2vw;
      }

      &:hover {
        color: #fff;
        background-color: #979899;
      }
    }

    input {
      position: absolute;
      width: .1px;
      top: -999999px;
    }
  }

  &.isMobile {
    min-height: auto;
    height: 100%;

    .title-container {
      height: 67px;

      .title {
        font-size: 2.1rem;
        margin-bottom: 2vh;
      }
    }
  }
}