.message-wrapper {
  width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, .1);

  &.ai {
    background-color: rgba(247, 247, 248, 1);
  }

  .message {
    align-items: flex-start;
    display: flex;
    gap: .5rem;
    margin: auto;
    justify-content: space-between;
    padding-bottom: 1.5rem;
    padding-top: 1.5rem;

    .wrapper {
      flex: 1 1;
      flex-wrap: wrap;
      overflow: hidden;
      width: 100vw;

      .text-left {
        text-align: left;
      }

      .text-right {
        text-align: right;
      }

      p.blink {
        animation: blink-2 1.3s both infinite;

        @-webkit-keyframes blink-2 {
          0% {
            opacity: 1
          }

          50% {
            opacity: .2
          }

          100% {
            opacity: 1
          }
        }

        @keyframes blink-2 {
          0% {
            opacity: 1
          }

          50% {
            opacity: .2
          }

          100% {
            opacity: 1
          }
        }
      }

      .markdown {
        border-radius: 0.5rem;
        font-size: 0.8rem;
        padding: 1rem;
      }

      .createdAt {
        color: rgb(71, 85, 105);
        font-size: 0.8rem;
        font-weight: 100;
        margin-top: .3vh;
        line-height: 1.25rem;
      }

      &.image {
        display: flex;
        justify-content: space-between;

        >div {
          width: 49%;
          position: relative;
        }

        .img {
          max-width: 100%;
          height: auto;
          border-radius: 0.5rem;
          vertical-align: top;
        }
      }
    }

    .pic {
      align-items: center;
      background-color: #dbdbdb;
      border-radius: 50%;
      display: flex;
      font-size: 1.25rem;
      height: 2.5rem;
      justify-content: center;
      line-height: 1.75rem;
      margin-right: 0.8rem;
      width: 2.5rem;

      .bot {
        max-width: 75%;
        height: auto;
      }

      .rounded-full {
        width: 2.5rem;
        height: 2.5rem;
        border-radius: .125rem;
      }
    }

    &.isMobile {
      padding: 1rem;

      .wrapper {
        .markdown {
          font-size: 1rem;
          line-height: 1.2rem;
        }

        &.image {}
      }

      .pic {
        margin-right: 0.4rem;
        width: 2rem;
        height: 2rem;
      }

      .rounded-full {
        width: 2rem;
        height: 2rem;
      }
    }
  }
}

.modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 100;

  .close-btn {
    position: fixed;
    top: 1%;
    right: 1%;
    font-size: 3rem;
    color: #fff;
    cursor: pointer;

    .close-icon {}
  }

  .image-wrapper {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 60%;
    }
  }
}

@media screen and (min-width: 1280px) {
  .message-wrapper {
    .message {
      max-width: 48rem;
    }
  }
}

@media screen and (max-width: 1280px) {
  .message-wrapper {
    .message {
      max-width: 42rem;
    }
  }
}

@media screen and (max-width: 1010px) {
  .message-wrapper {
    .message {
      max-width: auto;
      padding: 1rem;
    }
  }
}